import {
    MENU_ACTION,
    MENU_TOGGLE_ACTION,
    MENU_NUM_LOGGED_IN_ACTION,
} from './../actionTypes/menu';

export const menuAction = (details = null) => dispatch => {
    dispatch({
        type: MENU_ACTION,
        payload: details,
    });
};

export const menuToggleAction = (details = null) => dispatch => {
    dispatch({
        type: MENU_TOGGLE_ACTION,
        payload: details,
    });
};

export const menuNumLoggedInAction = (count = 0) => dispatch => {
    dispatch({
        type: MENU_NUM_LOGGED_IN_ACTION,
        payload: count,
    });
};
